body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.breed-select {
  width: 300px;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  text-align: center;
  color: red;
  margin-top: 20px;
  font-weight: bold;
  display: none;
}

.cat-container {
  justify-content: center;
  align-items: flex-start;
  margin-top: 20px;
  display: flex;
}

.cat-image {
  max-width: 600px;
  height: auto;
  margin-right: 20px;
}

.cat-details {
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start;
  display: flex;
}

.cat-title {
  margin: 0 0 5px;
  font-size: 20px;
}

.cat-temperament {
  margin-top: 10px;
  font-weight: bold;
}

.cat-description, .cat-temperament {
  max-width: 600px;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

